import { useState, useRef, useEffect, useCallback } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate, useLocation } from "react-router-dom";
import { ArrowLeft, Sparkles, Send, Loader2, ChefHat, Utensils, Bookmark, Clock, Users, Image } from "lucide-react";
import AnimatedLoadingText from "@/components/ai-recipe/AnimatedLoadingText";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Skeleton } from "@/components/ui/skeleton";
import { Badge } from "@/components/ui/badge";
import { useLoginRequired } from "@/hooks/useLoginRequired";
import { useSavedRecipes } from "@/hooks/useSavedRecipes";
import { AIRecipeSuggestion } from "@/hooks/useAIRecipe";
import SavedRecipeDetailDialog from "@/components/ai-recipe/SavedRecipeDetailDialog";
import Markdown from "react-markdown";

type Message = { role: "user" | "assistant"; content: string };

// Tracks image loading state per recipe (keyed by recipe_name)
type RecipeImageMap = Record<string, { url: string | null; loading: boolean }>;

const CHAT_URL = `${import.meta.env.VITE_SUPABASE_URL}/functions/v1/ai-recipe/chat`;
const IMAGE_URL = `${import.meta.env.VITE_SUPABASE_URL}/functions/v1/ai-recipe/generate-images`;

const QUICK_PROMPTS = [
  { key: "quick1", zh: "今晚想吃清淡的，推荐一道汤", en: "Recommend a light soup for dinner", pt: "Recomende uma sopa leve para o jantar" },
  { key: "quick2", zh: "红烧肉怎么做？", en: "How to make braised pork belly?", pt: "Como fazer barriga de porco?" },
  { key: "quick3", zh: "适合新手的家常菜", en: "Easy homestyle dishes for beginners", pt: "Pratos caseiros fáceis para iniciantes" },
];

/** Extract recipe JSON blocks from message content */
const extractRecipes = (content: string): AIRecipeSuggestion[] => {
  const recipes: AIRecipeSuggestion[] = [];
  const regex = /```recipe-json\s*\n([\s\S]*?)```/g;
  let match;
  while ((match = regex.exec(content)) !== null) {
    try {
      const parsed = JSON.parse(match[1].trim());
      if (parsed.recipe_name && parsed.ingredients && parsed.steps) {
        recipes.push(parsed as AIRecipeSuggestion);
      }
    } catch {
      // skip invalid JSON
    }
  }
  return recipes;
};

/** Remove recipe-json code blocks from display content (both complete and partial/streaming) */
const stripRecipeJson = (content: string): string => {
  let stripped = content.replace(/```recipe-json\s*\n[\s\S]*?```/g, "");
  stripped = stripped.replace(/```recipe-json[\s\S]*/g, "");
  return stripped.trim();
};

// Mini recipe card shown inline in chat
const RecipeCard = ({
  recipe,
  imageState,
  onView,
  onSave,
  isSaved,
  isSaving,
  t,
  i18nLang,
}: {
  recipe: AIRecipeSuggestion;
  imageState: { url: string | null; loading: boolean } | undefined;
  onView: () => void;
  onSave: () => void;
  isSaved: boolean;
  isSaving: boolean;
  t: (key: string, fallback: string, opts?: any) => string;
  i18nLang: string;
}) => {
  const name =
    i18nLang === "en" && recipe.recipe_name_en
      ? recipe.recipe_name_en
      : i18nLang === "pt" && (recipe as any).recipe_name_pt
      ? (recipe as any).recipe_name_pt
      : recipe.recipe_name;

  const isLoading = imageState?.loading ?? false;
  const imageUrl = imageState?.url ?? null;

  return (
    <div
      className="rounded-xl border border-border overflow-hidden cursor-pointer hover:border-primary/50 transition-colors bg-background mt-2"
      onClick={onView}
    >
      {/* Image area */}
      <div className="relative w-full aspect-[16/9] bg-muted overflow-hidden">
        {isLoading ? (
          <>
            <Skeleton className="absolute inset-0 w-full h-full rounded-none" />
            <div className="absolute inset-0 flex flex-col items-center justify-center gap-1.5 z-10">
              <Sparkles className="h-4 w-4 text-primary animate-pulse" />
              <span className="text-[10px] text-muted-foreground">
                {t("aiRecipe.generatingImage", "AI 正在生成图片...")}
              </span>
            </div>
          </>
        ) : imageUrl ? (
          <img
            src={imageUrl}
            alt={name}
            className="w-full h-full object-cover"
            loading="lazy"
          />
        ) : (
          <div className="absolute inset-0 flex items-center justify-center">
            <Image className="h-8 w-8 text-muted-foreground/30" />
          </div>
        )}
      </div>

      {/* Info */}
      <div className="px-3 py-2.5 space-y-1.5">
        <p className="font-semibold text-sm leading-tight">{name}</p>
        <div className="flex items-center gap-2 flex-wrap">
          {recipe.cook_time && (
            <Badge variant="outline" className="gap-1 text-[10px] h-5 px-1.5">
              <Clock className="h-2.5 w-2.5" />
              {recipe.cook_time} {t("recipe.minutes", "分钟")}
            </Badge>
          )}
          {recipe.servings && (
            <Badge variant="outline" className="gap-1 text-[10px] h-5 px-1.5">
              <Users className="h-2.5 w-2.5" />
              {recipe.servings} {t("recipe.servingsUnit", "人份")}
            </Badge>
          )}
        </div>

        {/* Actions */}
        <div className="flex gap-2 pt-0.5" onClick={(e) => e.stopPropagation()}>
          <Button variant="outline" size="sm" className="gap-1.5 h-7 text-xs flex-1" onClick={onView}>
            <Utensils className="h-3 w-3" />
            {t("aiRecipe.viewRecipe", "查看食谱")}
          </Button>
          <Button
            variant="ghost"
            size="sm"
            className={`gap-1 h-7 text-xs ${isSaved ? "text-primary" : "text-muted-foreground"}`}
            onClick={onSave}
            disabled={isSaving || isSaved}
          >
            <Bookmark className={`h-3 w-3 ${isSaved ? "fill-primary" : ""}`} />
            {isSaved ? t("aiRecipe.saved", "已保存") : t("aiRecipe.save", "保存")}
          </Button>
        </div>
      </div>
    </div>
  );
};

const WhatToEat = () => {
  const { t, i18n } = useTranslation();
  const navigate = useNavigate();
  const location = useLocation();
  const backTo = (location.state as any)?.from || "/ai-recipe";
  const { requireLogin } = useLoginRequired();
  const { saveRecipe, isSaving, savedRecipes } = useSavedRecipes();

  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [previewRecipe, setPreviewRecipe] = useState<AIRecipeSuggestion | null>(null);
  const [previewOpen, setPreviewOpen] = useState(false);
  const [previewImageUrl, setPreviewImageUrl] = useState<string | null>(null);
  const [savedRecipeNames, setSavedRecipeNames] = useState<Set<string>>(new Set());
  // Per-message recipe images: messageIndex -> recipe_name -> {url, loading}
  const [recipeImages, setRecipeImages] = useState<Record<number, RecipeImageMap>>({});
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const textareaRef = useRef<HTMLTextAreaElement>(null);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const getQuickText = (prompt: typeof QUICK_PROMPTS[0]) => {
    if (i18n.language === "en") return prompt.en;
    if (i18n.language === "pt") return prompt.pt;
    return prompt.zh;
  };

  /** Fetch images for recipes in a message and update state */
  const fetchImagesForMessage = useCallback(async (msgIndex: number, recipes: AIRecipeSuggestion[]) => {
    if (recipes.length === 0) return;

    // Mark all as loading
    setRecipeImages(prev => ({
      ...prev,
      [msgIndex]: Object.fromEntries(recipes.map(r => [r.recipe_name, { url: null, loading: true }])),
    }));

    try {
      const resp = await fetch(IMAGE_URL, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${import.meta.env.VITE_SUPABASE_PUBLISHABLE_KEY}`,
        },
        body: JSON.stringify({ recipes }),
      });

      if (!resp.ok) throw new Error("Image generation failed");
      const data = await resp.json();
      const images: { recipe_name: string; image_url: string | null }[] = data.images || [];

      setRecipeImages(prev => ({
        ...prev,
        [msgIndex]: Object.fromEntries(
          images.map(img => [img.recipe_name, { url: img.image_url, loading: false }])
        ),
      }));
    } catch {
      // On error, mark as not loading (no image)
      setRecipeImages(prev => ({
        ...prev,
        [msgIndex]: Object.fromEntries(recipes.map(r => [r.recipe_name, { url: null, loading: false }])),
      }));
    }
  }, []);

  const sendMessage = async (text: string) => {
    if (!text.trim() || isLoading) return;

    const userMsg: Message = { role: "user", content: text.trim() };
    const newMessages = [...messages, userMsg];
    setMessages(newMessages);
    setInput("");
    setIsLoading(true);

    let assistantSoFar = "";
    let assistantMsgIndex = -1;

    try {
      const resp = await fetch(CHAT_URL, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${import.meta.env.VITE_SUPABASE_PUBLISHABLE_KEY}`,
        },
        body: JSON.stringify({ messages: newMessages, language: i18n.language }),
      });

      if (!resp.ok) {
        const errData = await resp.json().catch(() => ({}));
        throw new Error(errData.error || `Error ${resp.status}`);
      }

      if (!resp.body) throw new Error("No response body");

      const reader = resp.body.getReader();
      const decoder = new TextDecoder();
      let textBuffer = "";

      while (true) {
        const { done, value } = await reader.read();
        if (done) break;
        textBuffer += decoder.decode(value, { stream: true });

        let newlineIndex: number;
        while ((newlineIndex = textBuffer.indexOf("\n")) !== -1) {
          let line = textBuffer.slice(0, newlineIndex);
          textBuffer = textBuffer.slice(newlineIndex + 1);
          if (line.endsWith("\r")) line = line.slice(0, -1);
          if (line.startsWith(":") || line.trim() === "") continue;
          if (!line.startsWith("data: ")) continue;
          const jsonStr = line.slice(6).trim();
          if (jsonStr === "[DONE]") break;
          try {
            const parsed = JSON.parse(jsonStr);
            const content = parsed.choices?.[0]?.delta?.content as string | undefined;
            if (content) {
              assistantSoFar += content;
              setMessages(prev => {
                const last = prev[prev.length - 1];
                if (last?.role === "assistant") {
                  assistantMsgIndex = prev.length - 1;
                  return prev.map((m, i) => i === prev.length - 1 ? { ...m, content: assistantSoFar } : m);
                }
                assistantMsgIndex = prev.length;
                return [...prev, { role: "assistant", content: assistantSoFar }];
              });
            }
          } catch {
            textBuffer = line + "\n" + textBuffer;
            break;
          }
        }
      }
    } catch (e) {
      console.error("Chat error:", e);
      setMessages(prev => [...prev, { role: "assistant", content: t("aiRecipe.chatError", "抱歉，请求失败，请重试。") }]);
    } finally {
      setIsLoading(false);
      // After streaming done, check if there are recipes and fetch images
      if (assistantMsgIndex >= 0 && assistantSoFar) {
        const recipes = extractRecipes(assistantSoFar);
        if (recipes.length > 0) {
          fetchImagesForMessage(assistantMsgIndex, recipes);
        }
      }
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      sendMessage(input);
    }
  };

  const handleViewRecipe = (recipe: AIRecipeSuggestion, msgIndex: number) => {
    const imgState = recipeImages[msgIndex]?.[recipe.recipe_name];
    setPreviewImageUrl(imgState?.url ?? null);
    setPreviewRecipe(recipe);
    setPreviewOpen(true);
  };

  const isRecipeSaved = (recipe: AIRecipeSuggestion) => {
    return savedRecipeNames.has(recipe.recipe_name) ||
      savedRecipes.some(s => s.recipe_name === recipe.recipe_name);
  };

  const handleSaveRecipe = async (recipe: AIRecipeSuggestion) => {
    if (!requireLogin()) return;
    if (isRecipeSaved(recipe)) return;
    await saveRecipe(recipe);
    setSavedRecipeNames(prev => new Set(prev).add(recipe.recipe_name));
  };

  return (
    <div className="fixed inset-0 top-14 flex flex-col bg-background z-10">
      {/* Header */}
      <div className="px-4 py-3 border-b bg-background shrink-0">
        <div className="flex items-center justify-between">
          <button onClick={() => navigate("/ai-recipe", { replace: true, state: { from: backTo } })} className="p-1 -ml-1 hover:bg-muted rounded-md">
            <ArrowLeft className="h-5 w-5" />
          </button>
          <div className="flex items-center gap-2">
            <Sparkles className="h-4 w-4 text-primary" />
            <h2 className="font-medium">{t("aiRecipe.whatToEat", "吃什么")}</h2>
          </div>
          <div className="w-6" />
        </div>
      </div>

      {/* Messages */}
      <div className="flex-1 overflow-y-auto p-4 max-w-lg mx-auto w-full space-y-4">
        {messages.length === 0 && (
          <div className="flex flex-col items-center justify-center py-12 space-y-6">
            <div className="w-16 h-16 rounded-full bg-primary/10 flex items-center justify-center">
              <ChefHat className="h-8 w-8 text-primary" />
            </div>
            <div className="text-center space-y-2">
              <h3 className="font-semibold text-lg">{t("aiRecipe.chatWelcome", "告诉我你想吃什么")}</h3>
              <p className="text-sm text-muted-foreground max-w-xs">
                {t("aiRecipe.chatWelcomeDesc", "描述你的口味偏好、食材要求或直接问某道菜怎么做，AI 为你推荐并教你烹饪")}
              </p>
            </div>
            <div className="flex flex-wrap gap-2 justify-center">
              {QUICK_PROMPTS.map((p) => (
                <button
                  key={p.key}
                  onClick={() => sendMessage(getQuickText(p))}
                  className="px-3 py-1.5 text-xs rounded-full border border-border hover:border-primary hover:bg-primary/5 transition-colors text-muted-foreground hover:text-foreground"
                >
                  {getQuickText(p)}
                </button>
              ))}
            </div>
          </div>
        )}

        {messages.map((msg, i) => {
          const recipes = msg.role === "assistant" ? extractRecipes(msg.content) : [];
          const displayContent = msg.role === "assistant" ? stripRecipeJson(msg.content) : msg.content;
          const msgImageMap = recipeImages[i];

          return (
            <div key={i} className="space-y-2">
              <div className={`flex ${msg.role === "user" ? "justify-end" : "justify-start"}`}>
                <div className={`max-w-[85%] rounded-2xl px-4 py-2.5 text-sm ${
                  msg.role === "user"
                    ? "bg-muted rounded-br-md"
                    : "bg-muted rounded-bl-md"
                }`}>
                  {msg.role === "assistant" ? (
                    <div className="prose prose-sm dark:prose-invert max-w-none [&>p]:mb-2 [&>ul]:mb-2 [&>ol]:mb-2 [&>h3]:mt-4 [&>h3]:mb-2 [&>hr]:my-3">
                      <Markdown>{displayContent}</Markdown>
                    </div>
                  ) : (
                    <p className="whitespace-pre-wrap">{displayContent}</p>
                  )}
                </div>
              </div>

              {/* Recipe cards with images */}
              {recipes.length > 0 && (
                <div className="pl-2 space-y-2 max-w-[85%]">
                  {recipes.map((recipe, ri) => (
                    <RecipeCard
                      key={ri}
                      recipe={recipe}
                      imageState={msgImageMap?.[recipe.recipe_name]}
                      onView={() => handleViewRecipe(recipe, i)}
                      onSave={() => handleSaveRecipe(recipe)}
                      isSaved={isRecipeSaved(recipe)}
                      isSaving={isSaving}
                      t={t as any}
                      i18nLang={i18n.language}
                    />
                  ))}
                </div>
              )}
            </div>
          );
        })}

        {isLoading && messages[messages.length - 1]?.role === "user" && (
          <div className="flex justify-start">
            <div className="bg-muted rounded-2xl rounded-bl-md px-4 py-3 flex flex-col gap-1.5 min-w-[160px]">
              <Loader2 className="h-4 w-4 animate-spin text-primary" />
              <AnimatedLoadingText
                messages={[
                  t("aiRecipe.chat.thinking.1", "正在思考..."),
                  t("aiRecipe.chat.thinking.2", "分析您的口味偏好..."),
                  t("aiRecipe.chat.thinking.3", "搜寻最佳菜品组合..."),
                  t("aiRecipe.chat.thinking.4", "为您精心准备推荐..."),
                ]}
                className="text-xs"
              />
            </div>
          </div>
        )}

        <div ref={messagesEndRef} />
      </div>

      {/* Input */}
      <div className="px-2 py-1.5 pb-[max(1.5rem,calc(env(safe-area-inset-bottom)+1rem))] md:pb-4 shrink-0 max-w-lg mx-auto w-full">
        <div className="flex items-center gap-1.5 px-2 py-1.5 rounded-full backdrop-blur-md border border-border/50 bg-muted/80 dark:bg-muted/60">
          <Textarea
            ref={textareaRef}
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyDown={handleKeyDown}
            placeholder={t("aiRecipe.chatPlaceholder", "输入你想吃的菜、口味偏好...")}
            className="flex-1 bg-transparent border-0 shadow-none focus-visible:ring-0 focus-visible:ring-offset-0 min-h-[24px] max-h-[120px] resize-none text-sm py-0.5"
            rows={1}
          />
          <Button
            size="icon"
            variant="ghost"
            onClick={() => sendMessage(input)}
            disabled={!input.trim() || isLoading}
            className="shrink-0 bg-transparent hover:bg-white/50 rounded-full"
          >
            {isLoading ? <Loader2 className="h-4 w-4 animate-spin" /> : <Send className="h-4 w-4" />}
          </Button>
        </div>
      </div>

      {/* Recipe preview dialog */}
      <SavedRecipeDetailDialog
        open={previewOpen}
        onOpenChange={setPreviewOpen}
        recipe={previewRecipe}
        recipeName={previewRecipe?.recipe_name || ""}
        onSave={handleSaveRecipe}
        isSaving={isSaving}
        isSaved={previewRecipe ? isRecipeSaved(previewRecipe) : false}
        initialImageUrl={previewImageUrl}
      />
    </div>
  );
};

export default WhatToEat;
